@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  border: none;
  transition: all 0.2s linear;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  background: #f7f7f7;
  height: 100vh;
  font-family: "Poppins", sans-serif;
}

/* round button start */
.round-btn {
  position: relative;
  margin-top: 2rem;
}
.round-btn .round-btn-icon {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
.round-btn .round-btn-icon li {
  display: inline-block;
  margin-bottom: 14px;
}
.round-btn-icon a {
  font-size: 2rem;
  display: inline-block;
  line-height: 44px;
  color: #fff;
  background-color: #2506ad;
  width: 44px;
  height: 44px;
  text-align: center;
  margin-right: 8px;
  border-radius: 100%;
  transition: all 0.2s linear;
}
.round-btn-icon a:active,
.round-btn-icon a:focus,
.round-btn-icon a:hover {
  background: #1a047e;
}
/* round button end */

#particles-js {
  display: none;
}

/* common media queries starts */
@media (max-width: 450px) {
  html {
    font-size: 55%;
  }
  body {
    padding-right: 0;
  }
  section {
    padding: 2rem;
  }
}
/* common media queries ends */

/* navbar starts */
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.7rem 10%;
  height: 6.5rem !important;
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(146, 161, 176, 0.3);
}
section {
  padding: 2rem 9%;
}
.heading {
  font-size: 3.5rem;
  font-weight: 800;
  text-align: center;
  padding: 1rem;
}
header .navbar ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
header .navbar li {
  margin-right: 2.5rem;
}
header .navbar ul li a {
  font-size: 1.57rem;
  color: #0e2431;
  font-weight: 600;
  text-decoration: none;
  letter-spacing: 0.04rem;
  transition: 0.2s;
}
header .navbar ul li a.active,
header .navbar ul li a:hover {
  color: #230b92;
  border-bottom: 0.2rem solid #230b92;
  padding: 0.5rem 0;
}
/* navbar ends */

/* hamburger icon starts*/
#menu {
  font-size: 3rem;
  color: rgb(24, 2, 63);
  display: none;
}
@media (max-width: 768px) {
  #menu {
    display: block;
  }
  header .navbar {
    position: fixed;
    top: 6.5rem;
    right: -120%;
    width: 75%;
    height: 100%;
    text-align: left;
    align-items: flex-start;
    background-color: #0e0f31;
  }
  header .navbar ul {
    flex-flow: column;
    padding: 1rem;
  }
  header .navbar ul li {
    text-align: center;
    width: 100%;
    margin: 1rem 0;
    border-radius: 0.5rem;
    width: 26rem;
  }
  header .navbar ul li a {
    display: block;
    padding: 1rem;
    text-align: left;
    color: #fff;
    font-size: 2rem;
  }
  header .navbar ul li a.active,
  header .navbar ul li a:hover {
    padding: 1rem;
    color: #fff;
    border-radius: 0.5rem;
    border-bottom: 0.5rem solid #011aff;
  }
  .fa-times {
    transform: rotate(180deg);
  }
  header .navbar.nav-toggle {
    right: 0;
  }
}
/* hamburger icon ends */
/* home section starts */
.home {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  min-height: 100vh;
  align-items: center;
  padding-top: 6.5rem;;
}
.home #particles-js {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: block;
}
.home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.home .content {
  flex: 1 1 40rem;
  padding-top: 1rem;
  z-index: 1;
}
.home .image {
  flex: 1 1 40rem;
  z-index: 1;
}
.home .image img {
  width: 60%;
  margin-left: 6rem;
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}
.home .content h3 {
  font-size: 5rem;
  font-weight: 800;
  color: #002057;
}
.home .content h3 span {
  font-size: 5rem;
  font-weight: 800;
  color: #ff7b00;
}
.home .content p {
  font-size: 2.5rem;
  color: #000;
  font-weight: 600;
  padding: 1rem 0;
}
.home .content p span {
  font-size: 2.5rem;
  color: rgb(148, 8, 8);
  font-weight: 600;
  padding: 1rem 0;
}
.home .btn {
  line-height: 4;
  padding: 1.6rem 3rem;
  border-radius: 4em;
  transition: 0.5s;
  color: #fff;
  background: #2506ad;
  box-shadow: 0px 5px 18px rgba(48, 68, 247, 0.6);
}
.home .btn span {
  font-weight: 700;
  font-size: 1.7rem;
  letter-spacing: 0.1rem;
}
.home .btn i {
  margin-left: 0.3rem;
  font-size: 1.5rem;
  transition: 0.6s;
}
.home .btn:hover {
  background: #1a047e;
}
.home .btn:hover i {
  transform: rotateX(180deg) translateX(5px);
}

/* home media queries starts */
@media (max-width: 450px) {
  .home .btn {
    margin: 4rem 0;
    text-align: start;
  }
  .round-btn {
    margin-top: 2rem;
  }
  .home .content p {
    font-size: 2.2rem;
  }
  .home .content p span {
    font-size: 2.2rem;
  }
}

@media (max-width: 780px) {
  .home {
    text-align: center;
  }

  .home .image img {
    width: 55%;
    margin-top: 1rem;
    margin-left: -1rem;
  }
}
/* home media queries ends */
/* home section end */
/* about section starts */
.about {
  background: linear-gradient(to bottom, #57059e, #4a00e0);
  color: #fff;
}
.about .container {
  display: flex;
  flex-direction: container;
  grid-gap: 2rem;
  gap: 2rem;
  flex-wrap: wrap;
  padding: 4rem;
}
.about .container .image {
  text-align: center;
  flex: 1 1 35rem;
}
.about .container .image img {
  margin: 4rem;
  width: 30rem;
  border-radius: 5%;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.6);
  mix-blend-mode: normal;
  transition: 0.3s;
}
.about .container .content {
  flex: 1 1 45rem;
  padding: 3rem;
}
.about .container .content h2 {
  font-size: 2.8rem;
  text-align: left;
  padding-top: 1.5rem;
  padding-left: 1rem;
}
.about .container .content h3 {
  font-size: 2rem;
  font-weight: 600;
  text-align: left;
  margin-left: 1rem;
}
.about .container .content p {
  font-size: 1.5rem;
  text-align: left;
  margin-left: 1rem;
}

/* about media queries starts*/
@media screen and (max-width: 600px) {
  .about .container .image {
    margin-top: 2rem;
  }
  .about .container .image img {
    margin: 0 auto;
    width: 80%;
    mix-blend-mode: normal;
  }
  .about .container {
    padding: 0.5rem;
    margin-bottom: 7rem;
  }
  .about .container .content {
    padding: 1rem;
  }
}
/* about media queries ends */
/* about section ends */
/* skills section starts */
.skills {
  background: #e5ecfb;
  color: #002057;
}
.skills .container {
  background: #a8a7e3;
  border-radius: 1rem;
  padding: 2rem;
  margin: 1rem;
}
.skills .container .row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  flex-wrap: wrap;
  margin-left: 4%;
  margin-top: 2%;
}
.skills .container .row .col {
  display: grid;
}
.skills .container .col {
  position: relative;
  width: 90%;
  color: #fff;
  margin-top: 0.5rem;
}
.skills .container .col .bar {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 1rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 22, 0.9);
  transition: 0.2s;
}
.skills .container .col .bar:hover {
  transform: translateX(12px);
  box-shadow: 0 5px 8px rgba(0, 2, 68, 0.4);
}
.skills .container .col .bar .info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}
.skills .container .col .bar .info i {
  font-size: 2rem;
}
.skills .container .col .bar .info span {
  font-size: 1.8rem;
  font-weight: 500;
  margin-left: 0.5rem;
}
.skills .container .col .bar .line {
  position: relative;
  width: 100%;
  height: 10px;
  background: rgb(212, 211, 211);
  border-radius: 6px;
}
.skills .container .col .bar .line:before {
  content: "";
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 4px;
}
.skills .container .col .bar .language:before {
  width: 100%;
  background: #e03d0b;
}
.skills .container .col .bar .database:before {
  width: 100%;
  background: #2962ff;
}
.skills .container .col .bar .tool:before {
  width: 100%;
  background: #f7d203;
}
.skills .container .col .bar .framework:before {
  width: 100%;
  background: #2fdf7e;
}

/* skills media queries starts */
@media screen and (max-width: 600px) {
  .skills .container {
    padding: 0;
    margin: 0;
  }
  .skills .container .row {
    grid-template-columns: repeat(1, 1fr);
    margin: 1rem;
    padding-top: 2rem;
  }
  .skills .container .col {
    margin-top: 5px;
    width: 100%;
  }
  .skills .container .col .bar {
    margin-bottom: 10px;
    padding: 10px;
  }
  .skills .container .col .bar .line {
    height: 0.8rem;
  }
}
/* skills media queries ends */
/* skills section ends */
/* education section starts */
.education {
  background: linear-gradient(to bottom, #57059e, #4a00e0);
  color: #fff;
}
.education .timeline {
  position: relative;
  max-width: 1200px;
  margin: 2rem auto;
}
.education .timeline::after {
  content: "";
  position: absolute;
  width: 6px;
  background: #fff;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}
.education .container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}
.education .container::after {
  content: "\f19d";
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  background-color: rgb(255, 255, 255);
  border: 4px solid #f68c09;
  top: 15px;
  border-radius: 50%;
  z-index: 100;
  font-size: 1.89rem;
  text-align: center;
  font-weight: 600;
  color: #02094b;
  font-family: "Font Awesome\ 5 Free";
}
.education .left {
  left: 0;
}
.education .right {
  left: 50%;
}
/* arrows pointing right */
.education .left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid #f68c09;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent #f68c09;
}
/* arrows pointing left  */
.education .right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid #f68c09;
  border-width: 10px 10px 10px 0;
  border-color: transparent #f68c09 transparent transparent;
}
.education .right::after {
  left: -16px;
}
.education .content {
  background-color: #f68c09;
  position: relative;
  border-radius: 6px;
}
.education .content .tag {
  padding-top: 1.5rem;
  padding-left: 1.5rem;
}
.education .content .tag h2 {
  font-size: 2rem;
}
.education .content .desc {
  margin-left: 1.5rem;
  padding-bottom: 1rem;
}
.education .content .desc h3 {
  font-size: 1.5rem;
  font-weight: 600;
}
.education .content .desc p {
  font-size: 1.2rem;
}

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
  .education .timeline::after {
    left: 31px;
  }
  .education .container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  .education .container::before {
    left: 60px;
    border: medium solid #f68c09;
    border-width: 10px 10px 10px 0;
    border-color: transparent #f68c09 transparent transparent;
  }
  .education .left::after {
    left: 15px;
  }
  .education .right::after {
    left: 15px;
  }
  .education .right {
    left: 0%;
  }
  .education .container::after {
    font-size: 2.2rem;
  }
}
/* education media queries ends */
/* education section ends */
/* projects section starts */
.projects {
  background: #e5ecfb;
  color: #002057;
}
.projects .box-container {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  margin: 2rem;
}
.projects .box-container .box {
  flex: 1 1 30rem;
  border-radius: 0.5rem;
  box-shadow: 0 0.7rem 1rem rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  height: 30rem;
  background: #fff;
  border: none;
}
.projects .box-container .box img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.projects .box-container .box .content {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 85%;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
}
.projects .box-container .box .content .tag {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 4.5rem;
  width: 100%;
  padding-left: 1rem;
  background: #e5ecfb;
  color: #002057;
}
.projects .box-container .box .content .tag h3 {
  font-size: 2rem;
}
.projects .box-container .box:hover .content {
  top: 25%;
}
.projects .desc {
  margin: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.projects .desc p {
  font-size: 1.5rem;
}
.projects .desc .btns {
  display: block;
  justify-content: space-between;
  width: 100%;
}
.projects .desc .btns .btn {
  line-height: 0;
  display: block;
  padding: 1.5rem 2.5rem;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  color: #fff;
  background: #2506ad;
  margin-top: 2rem;
}
.projects .desc .btns .btn:hover {
  background: #1a047e;
}
/* projects section ends */
